import React from 'react'
import { Container } from "./SliderElements"
import { Carousel } from "react-bootstrap"
import slider1 from "../../img/slider1.jpeg"
import slider2 from "../../img/slider2.jpeg"
import slider from "../../img/slider.jpeg"
import slider3 from "../../img/slider3.jpeg"


export const Slider = () => {
    return (
        <Container>

            <Carousel className="slider">

                <Carousel.Item interval={1000} style={{ maxWidth: "100vw", maxHeight: "100vh" }} className="itemSlider">
                    <div className="item">
                        <img
                            className="d-block w-100"
                            src={slider}
                            alt="First slide"
                            style={{ maxWidth: "100vw", maxHeight: "100vh", objectFit: "cover" }}
                        />
                    </div>
                    <Carousel.Caption style={{ top: "50%", transform: "translateY(-50%)", bottom: "initial" }}>
                        <div className="info-txt-slider">
                            <h3>As melhores peças você só encontra aqui.</h3>
                            <button>Conhecer coleção</button>
                        </div>
                    </Carousel.Caption>
                </Carousel.Item>



                <Carousel.Item interval={1000} style={{ maxWidth: "100vw", maxHeight: "100vh" }} className="itemSlider">
                    <div className="item">
                        <img
                            className="d-block w-100"
                            src={slider1}
                            alt="First slide"
                            style={{ maxWidth: "100vw", maxHeight: "100vh", objectFit: "cover" }}
                        />
                    </div>
                    <Carousel.Caption style={{ top: "50%", transform: "translateY(-50%)", bottom: "initial" }}>
                        <div className="info-txt-slider">
                            <h3>As melhores peças você só encontra aqui.</h3>
                            <button>Conhecer coleção</button>
                        </div>
                    </Carousel.Caption>
                </Carousel.Item>






                <Carousel.Item interval={1000} style={{ maxWidth: "100vw", maxHeight: "100vh" }} className="itemSlider">
                    <div className="item">
                        <img
                            className="d-block w-100"
                            src={slider2}
                            alt="First slide"
                            style={{ maxWidth: "100vw", maxHeight: "100vh", objectFit: "cover" }}
                        />
                    </div>
                    <Carousel.Caption style={{ top: "50%", transform: "translateY(-50%)", bottom: "initial" }}>
                        <div className="info-txt-slider">
                            <h3>Nós somos criativos em<span> Codó</span></h3>
                            <button>Conhecer coleção</button>
                        </div>
                    </Carousel.Caption>
                </Carousel.Item>





                <Carousel.Item interval={1000} style={{ maxWidth: "100vw", maxHeight: "100vh" }} className="itemSlider">
                    <div className="item">
                        <img
                            className="d-block w-100"
                            src={slider3}
                            alt="First slide"
                            style={{ maxWidth: "100vw", maxHeight: "100vh", objectFit: "cover" }}
                        />
                    </div>
                    <Carousel.Caption style={{ top: "50%", transform: "translateY(-50%)", bottom: "initial" }}>
                        <div className="info-txt-slider">
                            <h3>Nós somos criativos em<span> Codó</span></h3>
                            <button>Conhecer coleção</button>
                        </div>
                    </Carousel.Caption>
                </Carousel.Item>

            </Carousel>
        </Container>
    )
}
